import Vue from 'vue'

function getConfig (config, h) {
  return config instanceof Function ? config(h) : config
}

/**
 * 
 * @param creator      
 * @param wrap         
 * @param wrapConfig    
 * @param content      
 * @param contentConfig 
 */

export default function (creator, wrap, wrapConfig, content, contentConfig) {
  // 
  let instance = null
  // 
  let element = null

  const Instance = new Vue({
    name: 'v-insert',
    render: (h) => h(
      // 
      wrap,
      // 
      {
        ref: 'wrap',
        ...getConfig(wrapConfig, h)
      },
      // 
      content ? [
        h(
          // 
          content,
          {
            ref: 'content',
            ...getConfig(contentConfig, h)
          }
        )
      ] : []
    ),
    methods: {
      destroy () {
        this.$destroy()
        document.body.removeChild(element)
      },
      getCreator () {
        return creator
      }
    }
  })
  element = Instance.$mount().$el
  document.body.appendChild(element)
  instance = Instance.$children[0]
  return instance
}
