import { delay } from '../../utils/delay'
import { find, remove } from './messageInstance'

// 
export default async function Message (config) {
  const instance = find(this, { type: 'message' })
  await delay(50)
  instance.insert(config)
  instance.bindDestroy(remove)
  return instance
}

// 
Message.info = async (desc) => Message({ type: 'info', title: '', duration: 2.5, desc })

// 
Message.success = async (desc) => Message({ type: 'success', title: '', duration: 2.5, desc })

// 
Message.warning = async (desc) => Message({ type: 'warning', title: '', duration: 7.5, desc })

// 
Message.error = async (desc) => Message({ type: 'error', title: '', duration: 7.5, desc })
