const files = require.context('../prototypes/', true, /\w+\/index\.js/)

export default {
  install (Vue, options = {}) {
    files.keys().forEach(fileName => {
      // 
      const name = fileName.replace(/(^\.\/)|(\/index\.js)/ig, '')
      // 
      Vue.prototype[`$${name}`] = require(`${fileName}`).default
    })
  }
}
