import { delay } from '../../utils/delay'
import { find, remove } from '../message/noticeInstance'

// 
export default async function Notice (config) {
  const instance = find(this, { type: 'notice' })
  await delay(50)
  instance.insert(config)
  instance.bindDestroy(remove)
  return instance
}

// 
Notice.info = async (desc) => Notice({ type: 'info', title: '', duration: 2.5, desc })

// 
Notice.success = async (desc) => Notice({ type: 'success', title: '', duration: 2.5, desc })

// 
Notice.warning = async (desc) => Notice({ type: 'warning', title: '', duration: 7.5, desc })

// 
Notice.error = async (desc) => Notice({ type: 'error', title: '', duration: 7.5, desc })
